@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@700&display=swap');

body {
  margin: 0;
  font-family: 'Oswald', sans-serif;
  height: 100vh;
  opacity: 0;
  transition: opacity .3s ease;
  &.isLoaded {
    opacity: 1;
  }
}

canvas {
  display: block;
}

#container {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
}

.page {
  height: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  &__content {
    position: relative;
    z-index: 2;
  }
}

.list {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  &__item {
    padding: 40px 0;
  }
  &__item-title {
    text-transform: uppercase;
    margin: 0;
    font-size: 35px;
  }
  &__item-img {
    display: none;
  }
}
